import React from "react";
import Vestfold from "../../../images/Vestfold.png";

const Art6 = props => {
  return (
    <div
      className="ordal co"
      style={{
        left: `${props.scrollLeft}`
      }}
    >
      <div className="art">
        <img src={Vestfold} alt="" />
        <div className="text">
          <p>
            Norkart har bisttt Vestfold fylkeskommune med  fremskaffe komplett
            oversikt over eiendommer knyttet til fylkesveier i Vestfold.
          </p>
          <p>
            Analysen bestr av ca 1600 eiendommer. Eiendommene er bde festet,
            eiet, matrikulert, ikke matrikulert, bestr av flere teiger  med og
            uten riktig kontaktinstans (KE).
          </p>
          <p>
            Analysene ble systematisert slik at de kan sorteres og visualiseres
            utfra de ulike forutsetningene.
          </p>
          <p>
            Formlet med analysen var  skaffe fullstendig oversikt over
            eiendommene bde som grunnlag for orientering av politikerne,
            grunnlag for riktig registrering av eier og som grunnlag for
            utvelgelse av eiendommer for avhending.
          </p>
          <p>
            Norkart har lst oppdraget kompetent og fleksibelt og gitt
            fylkeskommunen som eier et godt verkty for analyse og en god
            oversikt over egne eiendommer til vegforml. Analyseresultatet
            benyttes som et effektivt verkty for god samhandling med Statens
            vegvesen ved forvaltning av eiendommene. Analysen avdekker feil og
            mangler ved registreringen av eiendommene som n ryddes opp i.
          </p>
          <p>Vi takker Norkart for godt smarbeid!</p>
        </div>
        <div className="author-infobox">
          <p className="author-name">
            <em>yvind Trygstad</em>
          </p>
          <p className="role">
            <em>rdgiver</em>
            <em>Eiendomsseksjonen/ Stabsavdelingen</em>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Art6;
