import React from "react";
import Ordal from "../../../images/Ordal.png";

const Art5 = props => {
  return (
    <div
      className="ordal co"
      style={{
        left: `${props.scrollLeft}`
      }}
    >
      <div className="art">
        <img src={Ordal} alt="" />
        <div className="text">
          <p>Les hele saken om Orkdal kommune og automatiseringslsninger</p>
          <p>
            Orkdal kommune valgte  ta i bruk Norkarts automatiseringslsninger
            for megleropplysninger vren 2018. Samtidig fikk vi testet ut felles
            ordregenerator. Vi opplevde at det tidligere ble brukt mye tid og
            ressurser p leveranser av megleropplysninger, og s et stor
            potensiale i at automatisering kunne frigjre ressurser. Vi opplevde
            fort at felles ordrebehandler var et meget godt verkty for
            administrering og behandling av ordrer. For oppgaver som ikke er
            automatisert, benytter vi oss av dokumentmaler. Disse malene
            effektiviserer ogs mange av de manuelle oppgavene. Ved bruk av
            Norkarts automatiseringslsninger og ordresystem har vi langt p vei
            halvert arbeidsmengden vi hadde med utlevering av
            megleropplysninger. Produktene vi leverer ut har n ogs blitt bedre
            og presenteres p en mer ryddig og oversiktlig mte. Vi har ftt
            flere gode tilbakemeldinger fra meglere p dette.
          </p>
          <p>
            Vi opplever i dag at kommunen ikke har styringen nr det gjelder
            utlevering av megleropplysninger. Mangel p pne lsninger har
            medfrt at vi har vrt ndt til  forholde oss til 3 ulike klienter
            for  behandle ordrer. Dette medfrer mye merarbeid for oss og vi
            nsker selv  velge hvordan vi utleverer data og hvordan ordrene
            skal behandles. Samtidig nsker vi at alle aktrer skal f lik
            tilgang og samme pris for de produktene vi leverer ut. Som en
            mellomstor kommune har vi ikke ressurser til  utvikle denne typen
            lsninger selv. Vi har derfor valgt  ta i bruk felles
            ordrebehandler for  ta tilbake styringen p utlevering av
            offentlig informasjon til meglerne. Skal vi f utnyttet
            automatisering og effektivisering av offentlig informasjon fult ut,
            er vi avhengig av  kunne sette premissene selv, noe vi tror vi kan
            f til gjennom felles ordrebehandler.
          </p>
        </div>
        <div className="author-infobox">
          <p className="author-name">
            <em>Christian Bonvik</em>
          </p>
          <p className="role">
            <em>Geodataansvarlig</em>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Art5;
