import React from "react";
import Alta from "../../../images/Alta.jpg";

const Art4 = props => {
  return (
    <div
      className="alta co"
      style={{
        left: `${props.scrollLeft}`
      }}
    >
      <div className="art">
        <img src={Alta} alt="" />
        <div className="text">
          <p>
            Vi i Alta kommune leverer all informasjon tilknyttet meglerpakker og
            lignende produkter gjennom e-Portal, og hovedtyngden av produktene
            er n automatisert gjennom Norkarts lsning. ePortal sitt
            brukergrensesnitt, tilrettelagte lsninger og tilpassede
            dokumentmaler er s gode og fleksible at vi bruker portalen ogs til
            leveranser av manuelle produkter som Legalpant, Mlebrev,
            Bygningstegninger og Brukstillatelse og ferdigattest.
          </p>
          <p>
            Norkart og automatiseringslsningen har bidratt til at vi har mer
            enn halvert arbeidstiden vi bruker p meglerpakke-leveranser, og at
            kvaliteten p leveransene vre er hyere. Med e-Portal og tilhrende
            automatisering er det mindre risiko for feil i produktene, og
            produktene som leveres fra oss har et mer ensartet oppsett i og med
            at de automatiserte og manuelle produktene er laget p samme mal. Vi
            har ogs ftt mulighet  forbedre og videreutvikle enkelte av vre
            produkter til stor glede for meglerne som bruker produktene.
          </p>
          <p>
            Da vi begynte samarbeidet med Norkart og tok i bruk deres
            automatiseringslsning startet vi forsiktig med bare noen f
            automatiserte produkter som vi selv kontrollerte og godkjente. N
            blir alle automatiserte produkter ogs automatisk godkjent slik at
            vi kun bruker tid p de fire manuelle produktene vre.
          </p>
        </div>
        <div className="author-infobox">
          <p className="author-name">
            <em>Roy Knutsen</em>
          </p>
          <p className="role">
            <em>Fagansvarlig Geodata</em>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Art4;
