import React from "react";
import Nittedal from "../../../images/Nittedal.png";

const Art3 = props => {
  return (
    <div
      className="balsfjord co"
      style={{
        left: `${props.scrollLeft}`
      }}
    >
      <div className="art">
        <img src={Nittedal} alt="" />
        <div className="text">
          <p>
            Nittedal kommune leverer meglerpakke til bde Norkart og Infoland
            (Ambita) sine lsninger. Vi tok i bruk automatisk produksjon fra
            Norkart for begge lsningene 2.januar 2017.
          </p>
          <p>
            Automatikken fungerer veldig bra og er svrt tidsbesparende. Vi er
            da ogs frre som bruker tid p  produsere opplysninger. Den gjr
            ogs at feilkildene er minimale og at en sikrer at det er
            opplysningene som er registrert som blir hentet. Dette gjr oss mer
            effektive og at de opplysningene som gr automatisk kan sluttfres
            og ekspederes raskere.
          </p>
        </div>
        <div className="author-infobox">
          <p className="author-name">
            <em>Dag Wgen</em>
          </p>
          <p className="role">
            <em>Avdelingsingenir</em>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Art3;
